.header {
   padding: 22px 0 11px;
   background-color: $white;

   &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 157px 0 57px; }

   &__bar-logo {
      position: relative;
      @include flexCenter;
      width: 150px;
      height: 150px;
      margin-top: -8px;
      padding: 25px;
      text-align: center;
      @include f('din400', 22);
      line-height: 1;
      letter-spacing: 0.025em;
      font-weight: 700;
      background-color: $light-green;
      color: $white;
      transition: .3s ease-in-out;
      &:hover {
         background-color: #01805b; }

      &::before {
         @include vcenter;
         z-index: 1;
         width: 76%;
         height: 76%;
         border: 3px solid $white;
         content: ''; }

      &-text {
         padding-top: 5px; } }

   &__main-logo {
      margin: 55px 0 40px 176px;
      @include hide-text; }

   &__link {
      $parent: &;
      position: relative;
      margin-top: 25px;
      color: $red;
      @include f('hel700', 30);
      border-bottom: em(2, 30) solid currentColor;
      line-height: 1;
      transition: .3s ease-in-out;

      &:hover {
         border-bottom-color: transparent; }

      &-icon {
         position: absolute;
         top: -0.85em;
         right: -0.25em;
         font-size: 18px;
         transition: .3s ease-in-out; } } }
