.popular {
   padding: 70px 0 17px;

   &__title {
      padding-right: 120px; }

   &__list {
      position: relative;
      z-index: 1;
      margin: -30px -70px 0 0;
      padding: 70px 0 40px;
      //overflow: hidden

      &-controls {
         position: absolute;
         top: -23px;
         right: 68px;
         z-index: 1;
         font-size: 0; }

      &-btn {
         $parent: &;
         display: inline-flex;
         align-items: center;
         justify-content: center;
         width: 50px;
         height: 50px;
         background-color: rgba(#9B9B9B, .1);
         border-radius: 50%;
         cursor: pointer;
         transition: .3s ease-in-out;

         &:not(.is-disabled):hover {
            box-shadow: inset 0 0 15px rgba($black, .2); }

         &_prev {
            margin-right: 3px; }
         &_next {}

         &-icon {
            stroke: $light-green;
            fill: none;
            @include fs(27);
            transition: .5s ease-in-out;

            #{$parent}_prev & {
               transform: scaleX(-1); }
            #{$parent}.is-disabled & {
               stroke: #9B9B9B; } } } }

   &__item {
      $parent: &;
      display: block;
      border-radius: 28px;
      overflow: hidden;
      flex-basis: percentage(490 / 1440);
      //width: percentage(490 / 1440)
      flex-grow: 0;
      flex-shrink: 0;
      box-shadow: 13px 7px 30px rgba(#7E7C97, .87);

      &-img-wrap {
         display: block;
         @include cover-img;
         @include ratio(490, 262);
         &::after {
            opacity: 0;
            @include coverdiv;
            background-color: rgba(#4A4A4A, .47);
            transition: .3s ease-in-out;
            content: '';
            #{$parent}:hover & {
               opacity: 1; } }

         img {
            @include coverdiv;
            width: 100%; } }

      &-title {
         opacity: 0;
         position: absolute;
         left: 10%;
         right: 10%;
         bottom: 10%;
         @include fs(25);
         color: $white;
         transition: .3s ease-in-out;
         transform: translateY(30px);
         #{$parent}:hover & {
            transform: translateY(0);
            opacity: 1; } } } }
