.category {
   $parent: &;
   position: relative;
   //padding: 0 20px
   box-shadow: 13px 7px 30px rgba(0, 0, 0, 0.21);
   border-radius: 22px;
   &.is-active {
      border-color: $light-green-2; }

   &_default {
      &:not(:first-child) {
         margin-top: 36px; } }

   &__head {
      padding: 22px 100px 27px 48px;
      &:hover {
         &::after {
            opacity: 1; } }
      &::after {
         position: absolute;
         top: -1px;
         left: -1px;
         right: -1px;
         bottom: -1px;
         opacity: 0;
         transition: .35s .15s ease-in-out;
         border: 2px solid $light-green-2;
         border-radius: 22px;
         pointer-events: none;
         content: ''; }
      &.is-open {
         &::after {
            opacity: 1; } }

      &-arrow {
         margin-top: 0;
         margin-right: 38px;
         stroke: $light-green; }

      &-icon {
         position: absolute;
         &.icon-salad-1 {
            right: 32px; }
         &.icon-cabbage {
            top: 27px;
            right: 41px; }
         &.icon-cucumber {
            right: 46px;
            top: 29px; }
         &.icon-cherry {
            top: 22px;
            right: 14px; }
         &.icon-chicken {
            top: 21px;
            right: 21px; }
         &.icon-potato {
            top: 17px;
            right: 23px; }
         &.icon-leaf {
            right: 20px;
            top: 20px; } } }


   &__title {
      text-align: left;
      color: $light-green; }

   &__content {
      &-inner {
         display: flex;
         align-items: flex-start;
         flex-wrap: wrap;
         padding: 5px 6px 56px 20px; } }

   &__comp {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      width: calc(25% - 15px);
      min-height: 262px;
      padding: 12px 20px 23px;
      border-radius: 22px;
      background-color: $white;
      cursor: pointer;
      transition: .3s ease-in-out;
      @include fs(16);

      #{$parent}_default & {
         margin-top: 15px;
         &:not(:nth-child(4n)) {
            margin-right: 15px; } }

      &:hover {
         box-shadow: 13px 7px 30px rgba(0, 0, 0, 0.21); }

      &-img-wrap {
         display: block;
         margin-bottom: 8px;
         height: 108px;
         @include cover-img;
         img {
            display: block;
            width: 100%; } }

      &-title {
         color: #717171;
         font-size: inherit; }

      &-portion {
         position: relative;
         display: flex;
         align-items: center;
         margin-top: auto;
         padding-left: 22px;
         color: #9b9b9b;
         &-icon {
            position: absolute;
            top: 5px;
            left: 0; }
         span {
            margin-right: 4px; } }

      &-visual {
         @include coverdiv;
         z-index: 1;
         border: 3px solid transparent;
         border-radius: 22px;
         transition: .3s ease-in-out;
         pointer-events: none; }

      &-control {
         display: none;

         &:checked ~ #{$parent}__comp-visual {
            border-color: $light-green;
            box-shadow: 13px 7px 30px rgba(0, 0, 0, 0.21); } } } }
