.ingreds {
   padding: 49px 22px 40px 38px;
   background-color: rgba(#E1E3F7, .24);
   border-width: 2px;
   border-color: $light-green-2;
   color: #626263;

   &__title {
      margin-bottom: 26px; }

   &__name-field {
      padding-left: 5px;
      padding-right: 15px;
      margin-left: -5px;
      margin-bottom: 27px; }

   &__list {
      margin-bottom: 52px; }

   &__item {
      display: flex;
      align-items: flex-start;

      &:not(:last-child) {
         margin-bottom: 22px; }

      &-text {
         flex: 1 1 auto;
         padding-right: 15px; }

      &-name {
         display: block;
         margin-bottom: 4px;
         @include fs(18); }

      &-portion {
         @include fs(15); } }

   &__count {
      display: flex;
      align-items: center;
      flex: 0 0 105px;
      margin-top: -6px;
      color: rgba(#373737, .44);

      &-control {
         width: 40px;
         background: 0;
         text-align: center;
         @include f('hel700', 20);
         color: inherit; }

      &-btn {
         width: 35px;
         height: 35px;
         line-height: 1;
         @include f('hel700', 20);
         color: inherit;
         border-radius: 50%;
         transition: .3s ease-in-out;

         &:hover {
            box-shadow: inset 0 0 10px currentColor; }

         &_minus {
            background-color: rgba(#9B9B9B, .16); }

         &_plus {
            background-color: #DCF2EA; }

         span {
            position: relative;
            top: -3px; } } }

   &__status {
      display: flex;
      align-items: center;
      margin: 0 0 37px -20px;
      padding: 16px 15px;
      border: 1px solid $light-green-2;
      border-radius: 22px;
      @include f('hel700', 22);
      text-align: center;

      &-title {
         flex: 1 1 auto;
         font-size: inherit; }

      &-count {
         flex: 0 0 110px; } }

   &__actions {
      display: flex;
      margin: 0 0 0 -22px;
      font-size: 0;

      &-btn {
         width: calc(50% - 10px);
         margin: 0 5px; } } }
