.form-group {
  position: relative;
  overflow: hidden;
}

.form-group__control {
  height: 38px;
  width: 100%;
  padding-left: 30px;
  font-family: "Helvetica-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 2.4rem;
  line-height: 1;
  border-bottom: 1px solid #979797;
  background: 0;
  color: #000;
}

.form-group__icon {
  position: absolute;
  top: 2px;
  left: 0;
}

.form-group__label, .form-group__error {
  position: absolute;
  top: 0;
  left: 40px;
  right: 0;
  bottom: 0;
  color: rgba(208, 2, 27, 0.34);
  font-family: "Helvetica-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 2.4rem;
  pointer-events: none;
  transition: .3s .05s ease-in-out;
}

.form-group.is-filled .form-group__label, .form-group.is-filled .form-group__error,
.form-group__control:focus ~ .form-group__label,
.form-group__control:focus ~ .form-group__error {
  transform: translateY(100%);
}

.form-group__error {
  transform: translateY(100%);
  color: #DC3E3F;
  transition: .3s ease-in-out;
}

.form-group.is-error .form-group__error {
  opacity: 1;
  transform: translateY(0);
}
