.form-group {
   $parent: &;
   position: relative;
   overflow: hidden;
   &__control {
      height: 38px;
      width: 100%;
      padding-left: 30px;
      @include f('hel700', 24);
      line-height: 1;
      border-bottom: 1px solid #979797;
      background: 0;
      color: #000; }

   &__icon {
      position: absolute;
      top: 2px;
      left: 0; }

   &__label {
      position: absolute;
      top: 0;
      left: 40px;
      right: 0;
      bottom: 0;
      color: rgba(#D0021B, .34);
      @include f('hel700', 24);
      pointer-events: none;
      transition: .3s .05s ease-in-out;

      #{$parent}.is-filled &,
      #{$parent}__control:focus ~ & {
         transform: translateY(100%); } }

   &__error {
      @extend .form-group__label;
      transform: translateY(100%);
      color: $red;
      transition: .3s ease-in-out;
      #{$parent}.is-error & {
         opacity: 1;
         transform: translateY(0); } } }
